/* ==============================
		Grid Container Styling 
================================*/
.grid-row {
 display: grid;
 grid-template-columns: repeat(12, 1fr);
 grid-gap: .95rem;
}



/*==================================
			 	Grid Item Styling
===================================*/
.grid-row [class|="col"] {
	grid-column: 1 / span 12;
}

/*==================================
			 			Normal 
===================================*/
.grid-row .col-1 {
	grid-column: auto / span 1;
}

.grid-row .col-2 {
	grid-column: auto / span 2;
}

.grid-row .col-3 {
	grid-column: auto / span 3;
