/* LAYOUT SECTION */
#admin {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: auto 40px;
  grid-template-areas:
    'h h h m m m m m m m m m'
    'f f f m m m m m m m m m';
  height: 100vh;
  width: 100vw;
  color: #f5f5f5;
  color: var(--theme-color);
  position: fixed;
}

#header {
  grid-area: h;
  background: #000080;
  background: var(--Aheader);
}

#main {
  grid-area: m;
  background: #113cfc;
  background: var(--Amain);
}

#footer {
  grid-area: f;
  background: #008080;
  background: var(--Afooter);
}

/* HEADER SECTION */
.admin-menu .menu-link {
  display: block;
  padding: 0.5rem 0.75rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  color: #f5f5f5;
  color: var(--theme-color);
  font-weight: 500;
  font-size: 1.1rem;
}

.admin-menu .menu-link:hover,
.admin-menu .menu-link.active {
  background: Var(--Amain);
}

/* MAIN SECTION */
#main {
  overflow-y: scroll;
  padding: 1rem 0.75rem;
}

.as-heading {
  font-size: 2.5rem;
}

#list-table {
  width: 100%;
  background: rgba(0, 0, 0, 0.144);
  border-radius: 0.5rem;
  -webkit-border-radius: 0.5rem;
  -moz-border-radius: 0.5rem;
  -ms-border-radius: 0.5rem;
  -o-border-radius: 0.5rem;
}

.page-header {
  display: flex;
  justify-content: space-between;
}

#list-table thead {
  background: rgba(0, 0, 0, 0.308);
}

#list-table th {
  padding: 0.5rem;
  margin-bottom: 0.75rem;
}

#list-table tbody td img {
  width: 100px;
  height: 80px;
}

#list-table tbody .btn {
  width: 100%;
  display: block;
}

#list-table tbody tr:nth-child(even) {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.068);
}

#list-table tbody .remove-btn {
  background: rgb(105, 5, 5);
}

#list-table tbody .edit-btn {
  background: rgb(0, 83, 0);
}

/* Settign form style */
.setting-menu-list .list-item {
  list-style-type: none;
  padding: 7px;
  background: rgba(53, 50, 50, 0.233);
  margin-bottom: 3px;
  display: flex;
  justify-content: space-between;
}

/* Product details text editor */

.pd-wrap {
  background: #fefefe;
}

.rdw-dropdown-optionwrapper {
  color: #333333;
}

.rdw-editor-main {
  color: #222;
  padding: 0.5rem;
}

